.story {
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    -webkit-box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
            box-shadow: 0px 5px 17px -7px rgba(0, 0, 0, 0.75);
    border-radius: 10px;
    margin-right: 10px;
    -webkit-transition: -webkit-transform 100ms ease-in;
    transition: -webkit-transform 100ms ease-in;
    -o-transition: transform 100ms ease-in;
    transition: transform 100ms ease-in;
    transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
    cursor: pointer;
}

.story:hover {
    -webkit-transform: scale(1.07);
        -ms-transform: scale(1.07);
            transform: scale(1.07);
}

.story__avatar {
    margin: 10px;
    border: 5px solid #2e81f4;
}

.story h4 {
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: white;
}


