.post {
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: white;
    -webkit-box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
            box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
}
.post__top {
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -ms-flex-align: center;
        align-items: center;
    padding: 15px;
}
.post__avatar {
    margin-right: 10px;
}

.post__topInfo h3 {
    font-size: medium;
}

.post__topInfo p {
    font-size: small;
    color: gray;
}

.post__bottom {
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 15px 25px;
}

.post__image img {
    width: 100%;
}

.post__options {
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 15px;
}

.post__option {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 5px;
    -ms-flex: 1 1;
        flex: 1 1;
}

.post__option p {
    margin-left: 10px;
}

.post__option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
}


