.header{
    display: -ms-flexbox;
    display: flex;
    padding: 15px 20px ;
    -ms-flex-pack: justify;
        justify-content: space-between;
    position: sticky;
    background-color: white;
    z-index: 100;
    top: 0;
    -webkit-box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0);
            box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0);
}


.header img {
    height: 40px;
}

.header__left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly; 
}

.header__left img {
    height: 40px;
}
.header__input {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background-color: #eff2f5;
    padding: 10px;
    margin-left: 10px;
    border-radius: 99px;
}

.header__input input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}

.header__center {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: center;
        justify-content: center;
}

.header__option .MuiSvgIcon-root {
    color: gray;
}

.header__option:hover .MuiSvgIcon-root {
    color: blue;
}

.header__option--active .MuiSvgIcon-root {
    color: blue;
}

.header__option--active {
    border-bottom: 4px solid blue;
}


.header__option {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 30px;
    cursor: pointer;
}

.header__option:hover {
    background-color: #eff2f5;
    border-radius: 10px;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 30px;
    border-bottom: none;
}

.header__info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.header__info h4 {
    margin-left: 10px;
}

.header__right {
    display: -ms-flexbox;
    display: flex;
}




